.swiper-container {
  width: 100vw;
  padding: 16px 0 24px 0; }

.swiper-slide {
  max-width: 240px;
  display: flex;
  justify-content: space-between;
  border-radius: 6px;
  text-decoration: none; }

.swiper-slide,
.swiper-slide-active {
  padding: 16px; }

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: -6px; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  margin: 0 12px;
  background-color: #DDDDDD;
  opacity: 1; }

.swiper-pagination-bullet-active {
  background-color: #B5B6BC; }

.section-services__swiper-container {
  padding: 0;
  margin: 24px 0 16px 0;
  width: 100vw; }

.section-services__swiper-wrapper {
  padding: 0 24px; }

.section-services__swiper-slide {
  width: auto;
  align-items: center;
  max-height: 40px;
  font-family: 'TT Lakes Medium';
  font-size: 16px;
  color: #7E7E82;
  white-space: nowrap; }
  .section-services__swiper-slide:first-child {
    min-width: 260px; }

.section-brand__swiper-container {
  width: 100vw; }

.section-brand__swiper-wrapper {
  padding: 0 24px; }

.section-brand__swiper-slide {
  align-items: center;
  max-height: 72px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA; }

.section-technics__swiper-container {
  width: 100vw; }

.section-technics__swiper-wrapper {
  padding: 0 24px; }

.section-technics__swiper-slide {
  min-height: 160px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA; }

.section-technics__logo-arrow {
  align-self: flex-end; }

.section-price__swiper-wrapper {
  padding: 0 0 0 24px; }

.section-price__swiper-slide {
  flex-direction: column;
  min-width: 260px;
  min-height: 200px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA; }

.section-price__slider-btn {
  position: absolute;
  align-self: flex-end;
  bottom: 16px; }

.section-price__slider-text {
  display: flex;
  flex-direction: column; }

@media (min-width: 560px) and (max-width: 3200px) {
  .swiper-container {
    display: none; } }
